import React, { Component } from 'react';
import Axios from 'axios';

class Login extends Component{
    constructor(props){
        super(props);
        this.state={
            userName:"Matt",
            userPassword:"eggs"
        };
        this.updateState = (event) =>{
            const value = event.target.value;
            const name = event.target.name;
            this.setState({
                [name]: value
            });
        }
        this.handleLogin = (event) =>{
            event.preventDefault();
            this.props.handleLogin(this.state.userName, this.state.userPassword);
        }
    }
    render(){
        const user = this.props.user;
        return(
            <div className="loginBox">
                <form onSubmit={this.handleLogin}>
                    userName: <br/>
                    <input type="text" name="userName" value={this.state.userName} onChange={this.updateState}/>
                     <br/>userPassword: <br/>
                    <input type="userPassword" name="userPassword" value={this.state.userPassword} onChange={this.updateState}/>
                    <br/>
                    <input type="submit" value="Submit"/>
                </form>
            </div>
        );
    }
}
export default Login;