import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';
// import HeadText from './HeadText';
import Login from './Login';
// import DisplayUser from './DisplayUser';
// import NotesForChar from './NotesForChar';
import Axios from 'axios';

class LoginPage extends Component {
  constructor(props){
    super(props);
    this.state ={
      loggedIn: false,
      user: "fail"
    }
    this.handleLogin = this.handleLogin.bind(this);
  }

  handleLogout = () => {
    this.setState({
      loggedIn: false, user: "fail"
    });
  }

  handleLogin = (userName, userPassword) => {
    var self = this;
    var url = `/getAllUsers`;
    let i = 0;
    Axios.get(url).then(function(response){
      var user2 = response.data.userName;
      console.log(user2);
      if(userName === response.data.userName){
        self.setState({loggedIn:true, user:user2});
      }else{
        alert("Wrong userPassword");
      }
    }).catch(function(error){
      alert("Wrong userName");
    });
  }
  render() {
    if(this.state.loggedIn===true){
      return (
        <div className="App">
        {/* <DisplayUser userName={this.state.user} handleLogout={this.handleLogout}/> */}
      </div>
      );
    }else{
      return(
      <div className="App2">
        <Login handleLogin={this.handleLogin}/>
      </div>
      );
    }
  }
}
export default LoginPage;